















.home h1 {
  margin-bottom: 15px;
}
.home .image {
  width: 10%;
}
.home .store_name {
  width: 30%;
}
.home td.store_name img {
  float: left;
  margin-right: 10px;
  margin-top: 1px;
  width: 24px;
  height: auto;
}
.home td.store_name strong {
  display: block;
  margin-bottom: .2em;
  font-size: 14px;
}








































.dokan-vendor-single {
  max-width: 850px;
}
.dokan-vendor-single .vendor-header .profile-banner {
  position: relative;
  height: 315px;
  border: 1px solid #dfdfdf;
  background: #fff;
  overflow: hidden;
}
.dokan-vendor-single .vendor-header .profile-banner img {
  height: 315px;
  width: auto;
}
.dokan-vendor-single .vendor-header .profile-info {
  display: flex;
}
.dokan-vendor-single .vendor-header .profile-icon {
  width: 200px;
  margin-right: 20px;
  position: relative;
}
.dokan-vendor-single .vendor-header .profile-icon .profile-icon-wrap {
  background: #fff;
  border: 5px solid #fff;
  height: 170px;
  position: absolute;
  width: 170px;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.1);
  padding: 3px;
  box-sizing: border-box;
  top: -70px;
  left: 30px;
}
.dokan-vendor-single .vendor-header .profile-icon .profile-icon-wrap img {
  height: auto;
  width: 100%;
}
.dokan-vendor-single .vendor-header .store-info {
  position: relative;
  width: calc(100% - 220px);
}
.dokan-vendor-single .vendor-header .store-info h2 {
  position: absolute;
  top: -65px;
  width: 100%;
  font-size: 2em;
  color: #fff;
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.8);
}




















